/* Estilos generales para el contenedor principal del login */
.login-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center; /* Centra verticalmente también */
  min-height: 100vh;
  background-color: #fff; /* Fondo blanco */
  padding: 20px;
}

/* Estilos para el contenedor del logo (centrado en la parte superior) */
.login-logo-container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 40px;
  width: 100%;
}

.login-logo {
  max-width: 180px;
  height: auto;
}

/* Estilos para el contenedor del formulario de login (centrado) */
.login-form-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 380px; /* Ancho del cuadro del formulario */
  padding: 40px;
  background-color: #e3f2fd; /* Azul claro muy suave como fondo del formulario */
  border-radius: 12px; /* Bordes más redondeados */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.15); /* Sombra más pronunciada */
}

/* Estilos para el título del login */
.login-title {
  font-size: 2.2em;
  font-weight: bold;
  color: #1e88e5;
  margin-bottom: 25px;
  border-bottom: 3px solid #64b5f6;
  padding-bottom: 12px;
  text-align: center;
}

/* Estilos para el grupo de input (icono + input) */
.login-input-group {
  display: flex;
  align-items: center;
  background-color: #bbdefb;
  border-radius: 8px;
  margin-bottom: 20px;
  padding: 12px;
  width: 100%;
  border: 1px solid #90caf9;
}

/* Estilos para el icono dentro del input */
.login-icon {
  margin-right: 12px;
  color: #1976d2;
  font-size: 1.3em;
}

/* Estilos para el input de texto */
.login-input {
  border: none;
  background-color: transparent;
  padding: 12px;
  width: 100%;
  font-size: 1em;
  color: #212121;
  outline: none;
}

/* Estilos para el contenedor de los botones */
.login-buttons {
  display: flex;
  flex-direction: column; /* Apilar los botones verticalmente */
  gap: 15px; /* Espacio entre los botones */
  margin-top: 20px; /* Espacio desde el último input */
  width: 100%; /* Ocupan el ancho del formulario */
}

/* Estilos para el botón primario (Iniciar Sesión) */
.login-button-primary {
  border: none;
  border-radius: 25px;
  padding: 14px 28px;
  font-size: 1.1em;
  font-weight: bold;
  cursor: pointer;
  text-align: center;
  text-decoration: none;
  background-color: #1e88e5; /* Cambiado a azul oscuro */
  color: white;
  transition: background-color 0.3s ease;
}

.login-button-primary:hover {
  background-color: #1565c0; /* Hover también en azul oscuro */
}

/* Estilos para el botón secundario (¿No tienes cuenta? Regístrate) */
.login-button-secondary {
  border: none;
  border-radius: 25px;
  width: 300px;
  padding: 14px 28px;
  font-size: 1.1em;
  font-weight: bold;
  cursor: pointer;
  text-align: center;
  text-decoration: none;
  background-color: #1e88e5; /* Mantenido en azul oscuro */
  color: white;
  transition: background-color 0.3s ease;
}

.login-button-secondary:hover {
  background-color: #1565c0; /* Hover también en azul oscuro */
  text-align: center;
}

/* Estilos para el mensaje de error */
.login-error-message {
  color: red;
  margin-bottom: 12px;
  text-align: center;
  font-size: 0.9em;
}