.hacker-news-dashboard-container {
    padding: 20px;
    background-color: #ffffff;
    border-radius: 10px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
    margin-bottom: 20px;
    transition: all 0.3s ease;
}

.hacker-news-dashboard-container h2 {
    color: #222;
    margin-bottom: 20px;
    font-size: 1.3rem;
}

.hacker-news-dashboard-container ul {
    list-style: none;
    padding: 0;
    margin: 0;
}

.hacker-news-dashboard-container li {
    padding: 15px 0;
    border-bottom: 1px solid #e0e0e0;
}

.hacker-news-dashboard-container li:last-child {
    border-bottom: none;
}

.hacker-news-dashboard-container .news-title {
    text-decoration: none;
    color: #0056b3;
    font-weight: 600;
    font-size: 1rem;
    display: inline-block;
    margin-bottom: 5px;
}

.hacker-news-dashboard-container .news-title:hover {
    text-decoration: underline;
}

.hacker-news-dashboard-container .news-meta {
    color: #666;
    font-size: 0.85rem;
    margin-bottom: 8px;
}

.hacker-news-dashboard-container .news-preview {
    color: #444;
    font-size: 0.9rem;
    line-height: 1.4;
}

.hacker-news-dashboard-container .error-message {
    color: #dc3545;
    font-weight: bold;
}
