.register-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  background-color: #fff; /* Fondo blanco */
  padding: 20px;
}

.register-title {
  font-size: 2.2em;
  font-weight: bold;
  color: #1e88e5; /* Azul brillante */
  margin-bottom: 25px;
  border-bottom: 3px solid #64b5f6; /* Azul claro */
  padding-bottom: 12px;
  text-align: center;
}

.register-input-group {
  display: flex;
  align-items: center;
  background-color: #bbdefb; /* Azul claro intenso */
  border-radius: 8px;
  margin-bottom: 20px;
  padding: 12px;
  width: 300px;
  border: 1px solid #90caf9; /* Azul claro sutil */
}

.register-icon {
  margin-right: 12px;
  color: #1976d2; /* Azul oscuro */
  font-size: 1.3em;
}

.register-input {
  border: none;
  background-color: transparent;
  padding: 12px;
  width: 100%;
  font-size: 1em;
  color: #212121; /* Gris oscuro */
  outline: none;
}

.register-buttons {
  display: flex;
  gap: 15px;
  margin-top: 25px;
  width: 300px;
}

.register-button {
  border: none;
  border-radius: 25px;
  padding: 14px 28px;
  font-size: 1.1em;
  font-weight: bold;
  cursor: pointer;
  flex-grow: 1;
  text-align: center;
  text-decoration: none;
  transition: background-color 0.3s ease;
}

.register-button-primary {
  background-color: #64b5f6; /* Azul claro */
  color: white;
}

.register-button-primary:hover {
  background-color: #42a5f5; /* Azul claro más brillante */
}

.register-button-secondary {
  background-color: #1e88e5; /* Azul brillante */
  color: white;
}

.register-button-secondary:hover {
  background-color: #1565c0; /* Azul oscuro */
}

.register-error-message {
  color: red;
  margin-bottom: 12px;
  text-align: center;
  font-size: 0.9em;
}

.register-success-message {
  color: green;
  margin-bottom: 12px;
  text-align: center;
  font-size: 0.9em;
}